import React, {Component} from 'react';

import {
    Navbar,
    NavbarToggler,
    Nav,
    Collapse,
    NavItem,
    NavLink
} from 'reactstrap';
import Link from "react-router-dom/es/Link";
import GoogleSignIn from "./GoogleSignIn";

class NavBar extends Component {

    constructor(props) {
        super(props);

        this.toggle = this.toggle.bind(this);
        this.state = {
            isOpen: false
        };
    }

    toggle() {
        this.setState({
            isOpen: !this.state.isOpen
        });
    }

    onSignIn(userProfile, accessToken) {
        console.log(userProfile)
    }

    signOut() {
        console.log(this.googleAuth);
        this.googleAuth.signOut();
    }

    render() {
        return (
            <div>
                <Navbar color="faded" light expand="md">
                    <Link class="navbar-brand" to="/home">
                        SuperProcrastinator
                    </Link>
                    <NavbarToggler onClick={this.toggle}/>
                    <Collapse isOpen={this.state.isOpen} navbar>
                        <Nav className="ml-auto" navbar>
                            <NavItem>
                                <NavLink href="https://github.com/MSkrzypietz/super-procrastinator">Github</NavLink>
                            </NavItem>
                            <NavItem>
                                <Link class="nav-link" to="/settings">Settings</Link>
                            </NavItem>
                            <NavItem>
                                <Link class="nav-link" to="/subscriptions">Subscriptions</Link>
                            </NavItem>
                            <NavItem>
                                <GoogleSignIn clientId="340186055169-0fe4bvqh1ce7i4q2fetr0parcv9odkvq"
                                              ref={g => this.googleAuth = g}
                                              onSuccess={this.onSignIn.bind(this)}
                                />
                            </NavItem>
                            <NavItem>
                                <button onClick={this.signOut.bind(this)}> Sign Out </button>
                            </NavItem>
                        </Nav>
                    </Collapse>
                </Navbar>
            </div>
        );
    }
}

export default NavBar;



